<template>
  <div class="wrap" ref="wrap">
    <div class="start-btn" v-if="showStartBtn">
      <el-button type="success" @click="start">ÂºÄÂßã</el-button>
    </div>
    <div class="ranking">
      <el-button type="info" icon="el-icon-sort" round size="small" @click="showRanking">ÊéíË°åÊ¶ú</el-button>
    </div>
    <div class="logo-box">
      <img class="logo" src="../assets/images/logo-b.png">
      Êòü‰∫ëË¥™ÂêÉËõá
    </div>
    <div class="copyright">
      Ê≥®ÊÑèÔºöËØ∑ÂÆâË£Ö <a href="https://github.com/ChengOrangeJu/WebExtensionWallet" target="_blank">WebExtensionWallet</a> ÂÆòÊñπÈí±ÂåÖÊèí‰ª∂ÂêéÊ∏∏Áé© Êòü‰∫ëË¥™ÂêÉËõá
    </div>

    <!--Êü•ÁúãËØ¶ÁªÜÂºπÊ°Ü-->
    <el-dialog title="Êòü‰∫ëË¥™ÂêÉËõáÊéíË°åÊ¶ú" :visible.sync="dialogRankingInfoVisible" width="50%" :modal="false">
      <el-table :data="gridData">
        <el-table-column property="index" label="ÊéíÂêç" width="60">
          <template slot-scope="scope">
            <el-tag size="medium">{{ scope.row.index }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column property="nickname" label="Áé©ÂÆ∂ÊòµÁß∞" width="150"></el-table-column>
        <el-table-column property="address" label="Áé©ÂÆ∂Âú∞ÂùÄ"></el-table-column>
        <el-table-column property="score" label="ÂàÜÊï∞" width="80"></el-table-column>
      </el-table>
    </el-dialog>

    <div class="main">
      <div
        class="map"
        :style="{
          'paddingTop': remainTB + 'px',
          'paddingBottom': remainTB + 'px',
          'paddingLeft': remainLR + 'px',
          'paddingRight': remainLR + 'px'
        }">
        <div
          class="map-row"
          v-for="row in mapLists"
          :key="row.key">
          <div
            class="map-col"
            v-for="col in row.colList"
            :key="col.key">
            <div
              class="snake-part"
              :class="[{
                'header': snake.indexOf(col.key) === 0,
                'body':
                  snake.indexOf(col.key) > -1 &&
                  snake.indexOf(col.key) !== 0 &&
                  snake.indexOf(col.key) !== snake.length - 1,
                'footer':
                  snake.indexOf(col.key) === snake.length - 1,
                'egg': col.isEgg
              }, dirClassName]"></div>
          </div>
        </div>
      </div>
    </div>



    <transition name="fade">
      <div class="pop-ups" v-show="showCountdown">
        <h1 class="countdown-wrap__text">{{ countdown }}</h1>
      </div>
    </transition>

    <transition name="fade">
      <div class="pop-ups" v-show="isShowPop">
        <div class="pop-ups__content">
          <p class="title">
            Ê∏∏ÊàèÁªìÊùü
          </p>
          <p class="desc">Êú¨Ê¨°ÂæóÂàÜ‰∏∫Ôºö{{ getScoreCount }}</p>
          <img class="code-img" src="../assets/images/code.jpg" alt="">
          <p class="desc" style="font-size: 14px">ÈïøÊåâÂÖ≥Ê≥®ÂÖ¨‰ºóÂè∑Ôºå‰∫ÜËß£‰∏Ä‰∏ã</p>    <button type="button" class="reStart" @click="reStart">
            ÈáçÊñ∞ÂºÄÂßã
          </button>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import Hammer from 'hammerjs'
import Nebulas from 'nebulas'
import NebPay from 'nebpay.js'

var Neb = Nebulas.Neb
var neb = new Neb(new Nebulas.HttpRequest('https://testnet.nebulas.io'))
var api = neb.api

const dappAddress = 'n1teJPvSWtex7p1uq7PBn1XEUZf5ZdDvpeG'

export default {
  data () {
    return {
      isShowPop: false,
      showCountdown: false,
      countdown: 3,
      // Ââ©‰Ωô‰∏ä‰∏ã
      remainTB: 0,
      // Ââ©‰ΩôÂ∑¶Âè≥
      remainLR: 0,
      // Ë°åÊï∞Èáè
      rowNumber: 0,
      // ÂàóÊï∞Èáè
      columnNumber: 0,
      mapLists: [],
      // ‰ΩçÁΩÆÂùêÊ†á
      x: 0,
      y: 0,
      // ÂêÉËõãÊï∞Èáè
      scoreCount: 0,
      // Ëõã‰ΩçÁΩÆ
      eggX: 0,
      eggY: 0,
      // ÊñπÂêë
      direction: 'up',
      // ÈÅøÂÖçÂêåÊó∂Â§öÊ¨°ÊîπÂèòÊñπÂêëÂè•ÊüÑ
      changeDir: true,
      // ËøêÂä®Êó∂Èó¥Âè•ÊüÑ
      moveTimer: null,
      snake: [],
      // ËõáÁöÑÈïøÂ∫¶
      snakeLen: 3,
      // ÈÄüÂ∫¶
      speed: 200,


      showStartBtn: 1,
      dialogRankingInfoVisible: false,
      gridData: [{
          index: 1,
          nickname: 'ÁéãÂ∞èËôé',
          address: 'n1MFuBiGQPYwFNvqEdxfcS2uiwxWjnbmctK',
          score: 80
        }, {
          index: 2,
          nickname: 'ÁéãÂ∞èËôé',
          address: 'n1MFuBiGQPYwFNvqEdxfcS2uiwxWjnbmctK',
          score: 80
        }, {
          index: 3,
          nickname: 'ÁéãÂ∞èËôé',
          address: 'n1MFuBiGQPYwFNvqEdxfcS2uiwxWjnbmctK',
          score: 80
        }, {
          index: 4,
          nickname: 'ÁéãÂ∞èËôé',
          address: 'n1MFuBiGQPYwFNvqEdxfcS2uiwxWjnbmctK',
          score: 80
        },{
          index: 5,
          nickname: 'ÁéãÂ∞èËôé',
          address: 'n1MFuBiGQPYwFNvqEdxfcS2uiwxWjnbmctK',
          score: 80
        }
      ]
    }
  },
  computed: {
    getScoreCount () {
      return this.scoreCount * 10
    },
    dirClassName () {
      switch (this.direction) {
        case 'up':
          return ''
        case 'down':
          return 'rotate180'
        case 'left':
          return 'rotate270'
        case 'right':
          return 'rotate90'
      }
    }
  },
  methods: {
    showRanking () {
      this.dialogRankingInfoVisible = true
    },
    // ËÆæÁΩÆÂú∞Âõæ‰ø°ÊÅØ
    setMapData () {
      for (let i = 0; i < this.rowNumber; i++) {
        let obj = {
          key: i,
          colList: []
        }
        for (let k = 0; k < this.columnNumber; k++) {
          let c = {
            key: `${i}-${k}`,
            isEgg: false
          }
          this.$set(obj.colList, k, c)
        }
        this.$set(this.mapLists, i, obj)
      }
    },
    // ÈöèÊú∫Êï∞
    random (min, max) {
      return Math.floor(Math.random() * (max - min + 1) + min)
    },
    // ÁîüÊàêËõã
    createNewEgg () {
      // ÈöèÊú∫Âá∫Êñ∞ÁöÑeggÁöÑ‰∏ãÊ†áÁöÑxÂíåyÂÄº
      this.eggX = this.random(0, this.rowNumber - 1)
      this.eggY = this.random(0, this.columnNumber - 1)

      if (this.snake.indexOf(this.mapLists[this.eggX].colList[this.eggY].key) > -1 || this.mapLists[this.eggX].colList[this.eggY].isEgg) {
        this.createNewEgg()
      } else {
        this.mapLists[this.eggX].colList[this.eggY].isEgg = true
      }
    },
    // ËõáËøêÂä®
    snakeMove () {
      // Ê†πÊçÆ‰∏äÈù¢ËÆæÁΩÆÁöÑÊñπÂêëÊù•ËÆæÁΩÆËõáÂ§¥ÁöÑ‰ΩçÁΩÆ
      switch (this.direction) {
        case 'left':
          --this.y
          break
        case 'right':
          ++this.y
          break
        case 'up':
          --this.x
          break
        case 'down':
          ++this.x
          break
      }
      // ÊòØÂê¶ÊíûÂ¢ô‰∫Ü
      if (this.x < 0 || this.y < 0 || this.x >= this.rowNumber || this.y >= this.columnNumber) {
        this.isShowPop = true
        clearInterval(this.moveTimer)

        this.showStartBtn = 0
        return
      }
      // ÊòØÂê¶ÂêÉÂà∞Ëá™Â∑±
      if (this.snake.indexOf(this.mapLists[this.x].colList[this.y].key) > -1) {
        this.isShowPop = true
        clearInterval(this.moveTimer)

        this.showStartBtn = 0
        return
      }
      // ÂêÉÂà∞Ëõã‰∫Ü
      if (this.eggX === this.x && this.eggY === this.y) {
        this.mapLists[this.x].colList[this.y].isEgg = false
        ++this.scoreCount
        ++this.snakeLen
        this.createNewEgg()
      }
      // ÂâçËøõ
      this.snake.unshift(this.mapLists[this.x].colList[this.y].key)
      // ‰øùÊåÅüêçÁöÑÈïøÂ∫¶
      if (this.snakeLen < this.snake.length) {
        this.snake.pop()
      }
    },
    setDirection (direction) {
      // ÂÖàÂà§Êñ≠ÊòØÂê¶ÈúÄË¶ÅÊîπÂèòÊñπÂêë,trueË°®Á§∫ÈúÄË¶Å,falseË°®Á§∫‰∏çÈúÄË¶Å
      if (!this.changeDir) return
      // ‰∏∫‰∫ÜÂêàÁêÜÂ§ÑÁêÜËõáÁöÑÁßªÂä®,ÈúÄË¶ÅÂà§Êñ≠ËõáÂ§¥ÂíåËõáË∫´
      // ÂÅáËÆæËõáÂêëÂè≥ÁßªÂä®,ÁÇπÊñπÂêëÈîÆÂ∑¶,Âè≥ÈîÆÈÉΩ‰∏çÈúÄË¶ÅÂÅöÂá∫ÂìçÂ∫î
      if (this.direction === 'right' && direction === 'left') return
      if (this.direction === 'left' && direction === 'right') return
      if (this.direction === 'up' && direction === 'down') return
      if (this.direction === 'down' && direction === 'up') return

      this.direction = direction
      this.changeDir = false
      setTimeout(() => {
        this.changeDir = true
      }, 30)
    },
    init () {
      let flag = 0
      // ‰ªéÁ¨¨Âá†Ë°åÂºÄÂßã
      this.x = this.random(this.snakeLen, this.rowNumber - this.snakeLen)
      // ‰ªéÁ¨¨Âá†‰∏™ÂºÄÂßã
      this.y = this.random(this.snakeLen, this.columnNumber - this.snakeLen)
      // ÂàõÂª∫üêç
      while (flag < this.snakeLen) {
        this.snake.push(this.mapLists[flag + this.x].colList[this.y].key)
        ++flag
      }
      // ÂàõÂª∫Ëõã
      this.createNewEgg()
    },
    eventInit () {
      // ÁîµËÑëÈîÆÁõò
      document.onkeydown = (e) => {
        e = e || e.event
        let direction = ''
        switch (e.keyCode) {
          case 37:
            // ÂêëÂ∑¶
            direction = 'left'
            break
          case 38:
            // Âêë‰∏ä
            direction = 'up'
            break
          case 39:
            // ÂêëÂè≥
            direction = 'right'
            break
          case 40:
            // Âêë‰∏ã
            direction = 'down'
            break
        }
        this.setDirection(direction)
      }
      // ÊâãÊú∫ÊâãÂäø
      var hammer = new Hammer(this.$refs.wrap)
      hammer.get('swipe').set({ direction: Hammer.DIRECTION_ALL })
      hammer
        .on('swipeleft', e => {
          this.setDirection('left')
        })
        .on('swiperight', e => {
          this.setDirection('right')
        })
        .on('swipeup', e => {
          this.setDirection('up')
        })
        .on('swipedown', e => {
          this.setDirection('down')
        })
    },
    // ËÆ°ÁÆóË°å‰∏éÂàóÁöÑÊï∞ÈáèÂíå‰∏ä‰∏ãÂ∑¶Âè≥Ââ©‰Ωô
    computeNum () {
      this.remainTB = (window.innerHeight % 20) / 2
      this.remainLR = (window.innerWidth % 20) / 2
      this.rowNumber = (window.innerHeight - window.innerHeight % 20) / 20
      this.columnNumber = (window.innerWidth - window.innerWidth % 20) / 20
    },
    startGame () {
      return new Promise((resolve, reject) => {
        this.showCountdown = true
        var timer = setInterval(() => {
          if (this.countdown > 1) {
            --this.countdown
          } else {
            this.showCountdown = false
            clearInterval(timer)
            this.countdown = 3
            resolve()
          }
        }, 1000)
      })
    },
    reStart () {
      location.reload()
    },
    cbPush (resp) {
      console.log("response of push: " + JSON.stringify(resp))
      var intervalQuery = setInterval(() => {
        api.getTransactionReceipt({hash: resp["txhash"]}).then((receipt) => {
            console.log("Âà§Êñ≠Êï∞ÊçÆÊèê‰∫§Âà∞Âå∫ÂùóÈìæÁöÑÁä∂ÊÄÅ")
            // console.log(receipt)
            if (receipt["status"] === 2) {
                console.log("pending.....")
            } else if (receipt["status"] === 1){
                console.log("‰∫§ÊòìÊàêÂäü......")
                //Ê∏ÖÈô§ÂÆöÊó∂Âô®
                clearInterval(intervalQuery)
            }else {
                console.log("‰∫§ÊòìÂ§±Ë¥•......")
                //Ê∏ÖÈô§ÂÆöÊó∂Âô®
                clearInterval(intervalQuery)
            }
        });
      }, 5000);

    },
    // ÊéíË°åÊ¶ú
    ranking () {
      console.log(222);
    },
    // ÂºÄÂßã
    start () {

      this.$prompt('ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÊòµÁß∞', 'ÊèêÁ§∫', {
          confirmButtonText: 'Á°ÆÂÆö',
          cancelButtonText: 'ÂèñÊ∂à',
          inputPattern: /\S/,
          inputErrorMessage: 'ÊòµÁß∞‰∏çËÉΩ‰∏∫Á©∫'
        }).then(({ value }) => {
          this.showStartBtn = 0
          this.startGame()
            .then(() => {
              this.eventInit()
              this.moveTimer = setInterval(this.snakeMove, this.speed)
            })
        }).catch(() => {

        });



      // this.$notify({
      //   title: 'ÊàêÂäü',
      //   message: 'ËøôÊòØ‰∏ÄÊù°ÊàêÂäüÁöÑÊèêÁ§∫Ê∂àÊÅØ',
      //   type: 'success'
      // })


      // test nas

      // var from = 'n1YLc2ndCuzS5hKthxvpBDtv1c1YPa6Pacw'
      // var value = '0'
      // var nonce = '0'
      // var gas_price = '1000000'
      // var gas_limit = '2000000'



      // // getÊü•ËØ¢
      // var contract = {
      //     "function": "get",
      //     "args": JSON.stringify(["tim44"])
      // }

      // neb.api.call(from,dappAddress,value,nonce,gas_price,gas_limit,contract).then( (resp) => {
      //       console.log("Êï∞ÊçÆÊü•ËØ¢ÂÆåÊàê", resp)
      //       if (resp["result"] !== "null") {

      //       } else {

      //       }
      //   }).catch(function (err) {

      //       console.log("error:" + err.message)
      //   })



      // save
      // var nebPay = new NebPay()

      // var value = "0";
      // var callFunction = "save"
      // var callArgs =JSON.stringify(["tim44", "ÂÖ®Ê†àÂ∑•Á®ãÂ∏à"])
      // console.log(callArgs)
      // nebPay.call(dappAddress, value, callFunction, callArgs, {    //‰ΩøÁî®nebpayÁöÑcallÊé•Âè£ÂéªË∞ÉÁî®ÂêàÁ∫¶,
      //     listener: this.cbPush        //ËÆæÁΩÆlistener, Â§ÑÁêÜ‰∫§ÊòìËøîÂõû‰ø°ÊÅØ
      // });




    }
  },
  watch: {
    // ÂàÜÊï∞Â¢ûÂä†ÔºåÈÄüÂ∫¶Âä†Âø´
    scoreCount (val, oldval) {
      // ÊØèÈïø10ÂàÜÂ∞±Â¢ûÂä†‰∏ÄÊ¨°ÈÄüÂ∫¶
      if (val > oldval && val % 6 === 0 && this.speed > 100) {
        this.speed -= 100
      }
    }
  },
  created () {
    this.computeNum()
    this.setMapData()
  },
  mounted () {
    this.init()
  }
}
</script>

